import { Component, OnInit } from '@angular/core';
import { QuemsomosService } from './quemsomos.service';

@Component({
  selector: 'app-quemsomos',
  templateUrl: './quemsomos.component.html',
  styleUrls: ['./quemsomos.component.css']
})
export class QuemsomosComponent implements OnInit {
  pessoas: any[]=[];
  constructor(public quemsomosservice:QuemsomosService) { }
  ngOnInit(){
    this.quemsomosservice.getPessoas().then(pessoas=>{
      this.pessoas = pessoas;
    }) 
  }
}
