import { ContatoComponent } from './components/pages/contato/contato.component';

import { ConstrucaoComponent } from './components/pages/construcao/construcao.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './components/pages/home/home.component';
import { QuemsomosComponent} from './components//pages/quemsomos/quemsomos.component';


const routes: Routes = [
  {path: '',   redirectTo: '/home', pathMatch: 'full'},
  {path: "home",
  component: HomeComponent},

  {path: "construcao",
  component: ConstrucaoComponent},

  {path:"quemsomos",
   component: QuemsomosComponent},

  {path: "contato",
   component: ContatoComponent},
  {path: '**', component: ConstrucaoComponent}

  

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
